import Base64 from 'js-base64';

export default 
{
  Mess(vm,mess) 
  {
    mess=JSON.parse(mess);
    let from=vm.$store.state.im.id;
    if(mess['type'])
    {
        switch(mess['type'])
        {
            case 'auth':
                if(mess['session_id'])
                {
                    vm.$store.state.im.socket.isLogined=true;
                    vm.$store.state.im.socket.session_id=mess['session_id'];
                    if(vm.$store.state.im.recive.length==0)
                    {
                        this.SendChatList(vm,vm.$conf.ChuanHai_ServieID,'friend',vm.$store.state.im.page+1);
                    }
                }
                else
                {
                    this.SendAuth(vm);
                }
                break;
            case 'user_mess':
                let k=mess.content['data'];
                for(let key in k)
                {
                    vm.$set(vm.$store.state.im.user, key.toString(), {'name':k[key]['name'],'pic':k[key]['avatar']});
                }
                break;
            case 'chat_list':
                //console.log(mess.content.list);
                var item=mess.content.list;

                if(parseInt(mess.content.page)>vm.$store.state.im.page){vm.$store.state.im.page=parseInt(mess.content.page);}
                vm.$store.state.im.isloading=false;
                if(!item || item.length==0)
                {
                    vm.$store.state.im.isfinished=true;
                }
                
                for(let i=0;i<item.length;i++)
                {
                    var t=item[i];//console.log(t);
                    if(!vm.$store.state.im.viewed.hasOwnProperty(t.mid.toString()))
                    {
                        var mess={from: t.from_user_id, to: t.id, type: "chat", content: {type:t.type, text: t.content,mid:t.mid,time:t.timestamp}, mid:t.mid.toString()};
                        if(!vm.$store.state.im.user[t.from_user_id.toString()])
                        {
                            vm.$store.state.im.user[t.from_user_id.toString()]={name:t.username,pic:t.avatar};
                        }
                        vm.$store.state.im.recive.unshift(mess);
                        vm.$store.state.im.viewed[t.mid.toString()]='';
                    }

                }
                //console.log(vm.$store.state.im.recive);
                break;
            case 'chat':
                //console.log(mess);

                let id=mess['mid'];
                vm.$store.state.im.recive.push(mess);
                if(vm.$route.path=='/im')
                {
                    this.SendQos(vm,4,id);
                    this.SendReciveQos(vm);
                    this.playMusic();
                }
                else
                {
                    if(!vm.$store.state.im.viewed.hasOwnProperty(mess.mid.toString()))
                    {
                        vm.$store.state.im.unRead.push(mess);
                        vm.$store.state.im.count=vm.$store.state.im.unRead.length;
                        this.SendQos(vm,3,id);
                        vm.$store.state.im.viewed[mess.mid.toString()]='';
                        this.playMusic();
                    }
                    //console.log(this.SendQos);
                    
                }
                //检测头像
                if(!vm.$store.state.im.user[mess['from'].toString()])
                {
                    this.SendCmd(vm,101,{'ids':mess['from'].toString()});
                }
                break;
        }
    }
    return;
    //{from: "0", to: "71.1", type: "auth", content: {account:"",password:""}, mid: ""}
  },
  SendQos(vm,type,mid) 
  {
    let from=vm.$store.state.im.id;
    vm.$socket.sendObj({"session_id":vm.$store.state.im.socket.session_id,"from":from,"to":"0","mid":"","type":"qos","content":{"type":type.toString(),"mid":mid.toString()}});
  },
  SendPing(vm) 
  {
    var that=this;
    setInterval(function()
    {
        //检测登录状态
        if(vm.$store.state.login.token && !vm.$store.state.im.socket.isConnected && ((Date.parse(new Date())-vm.$store.state.im.socket.lastLoginTime)/1000 >10))
        {
            that.Init(vm);
            vm.$connect();
        }
        //未授权
        if(vm.$store.state.im.socket.isConnected && (!vm.$store.state.im.socket.session_id || vm.$store.state.im.socket.session_id==''))
        {
            that.SendAuth(vm);
        }
        //ping
        let from=vm.$store.state.im.id;
        if(vm.$store.state.im.socket.isConnected && vm.$store.state.im.socket.session_id)
        {
            if(vm.$store.state.login.token==0){this.$disconnect();}
            vm.$socket.sendObj({"session_id":vm.$store.state.im.socket.session_id,"from":from,"to":"0","mid":"","type":"ping","content":""});
        }
    }.bind(vm,that), 7000);
  },
  Init(vm) {
    vm.$store.state.im.id=vm.$store.state.login.username+'.'+vm.$conf.ChuanHai_DomainID;
    vm.$store.state.im.unRead=[];
    vm.$store.state.im.recive=[];
    vm.$store.state.im.page=0;
    vm.$store.state.im.isfinished=false;
    vm.$store.state.im.isloading=false;
    vm.$store.state.im.viewed=[];
    vm.$store.state.im.count=vm.$store.state.im.unRead.length;

    vm.$options.sockets.onmessage = (data) => this.Mess(vm,data.data);
    vm.$options.sockets.onopen = (data) => this.SendAuth(vm);
    this.SendPing(vm);
  },
  SendReciveQos(vm) {
    if(vm.$store.state.im.unRead.length>0)
    {
        for(let i=0;i<vm.$store.state.im.unRead.length;i++)
        {
            this.SendQos(vm,4,vm.$store.state.im.unRead[i]['mid']);
        }
        vm.$store.state.im.unRead=[];
        vm.$store.state.im.count=vm.$store.state.im.unRead.length;
    }
  },
  SendAuth(vm)
  {
    if(!vm.$store.state.im.socket.isLogined && vm.$store.state.login.token==1)
    {
        let from=vm.$store.state.im.id;
        let str={userid:from,password:vm.$store.state.login.password};
        str=Base64.Base64.encode(JSON.stringify(str));
        vm.$socket.sendObj({from:from,to:"0",type:"auth",content:{account:str,password:"123"}});
        this.SendCmd(vm,101,{'ids':from+','+vm.$conf.ChuanHai_ServieID});
    }
  },
  SendMess(vm,message) {
    if(vm.$store.state.login.token!=1)
    {
        vm.$viewMess("请登录会员后,发送消息");
        return;
    }
    let from=vm.$store.state.im.id;
    let mid=this.MakeMid(vm);
    let json={"session_id":vm.$store.state.im.socket.session_id,"from":from,"to":vm.$conf.ChuanHai_ServieID,"mid":mid,"type":"chat","content":{"type":"text","text":message.toString(),"mid":mid.toString()}};
    vm.$socket.sendObj(json);
    vm.$store.state.im.recive.push(json);
  },
  SendChatList(vm,toid,type,page) {
    if(vm.$store.state.login.token!=1){return;}
    vm.$store.state.im.isloading=true;
    let from=vm.$store.state.im.id;
    let json={"session_id":vm.$store.state.im.socket.session_id,"from":from,"to":0,"mid":"","type":"cmd","content":{"type":100,"page":page.toString(),"chat_type":type.toString(),"toid":toid.toString()}};
    vm.$socket.sendObj(json);
  },
  MakeMid(vm)
  {
      return vm.$store.state.im.id.toString() + Date.parse(new Date()).toString();
  },
  SendCmd(vm,CmdType,para=null) {
    if(vm.$store.state.login.token!=1){return;}
    let from=vm.$store.state.im.id;
    let json={"session_id":vm.$store.state.im.socket.session_id,"from":from,"to":0,"mid":"","type":"cmd","content":{"type":CmdType.toString()}};
    if(para)
    {
        for(let i in para) 
        {
            json['content'][i.toString()]=para[i.toString()].toString();
        }
    }
    vm.$socket.sendObj(json);
  },
  html(end){//支持的html标签
    return new RegExp('\\n*\\['+ (end||'') +'(pre|div|p|table|thead|th|tbody|tr|td|ul|li|ol|li|dl|dt|dd|h2|h3|h4|h5)([\\s\\S]*?)\\]\\n*', 'g');
  },
  TxtFace(face){
    var alt = ["[微笑]", "[嘻嘻]", "[哈哈]", "[可爱]", "[可怜]", "[挖鼻]", "[吃惊]", "[害羞]", "[挤眼]", "[闭嘴]", "[鄙视]", "[爱你]", "[泪]", "[偷笑]", "[亲亲]", "[生病]", "[太开心]", "[白眼]", "[右哼哼]", "[左哼哼]", "[嘘]", "[衰]", "[委屈]", "[吐]", "[哈欠]", "[抱抱]", "[怒]", "[疑问]", "[馋嘴]", "[拜拜]", "[思考]", "[汗]", "[困]", "[睡]", "[钱]", "[失望]", "[酷]", "[色]", "[哼]", "[鼓掌]", "[晕]", "[悲伤]", "[抓狂]", "[黑线]", "[阴险]", "[怒骂]", "[互粉]", "[心]", "[伤心]", "[猪头]", "[熊猫]", "[兔子]", "[ok]", "[耶]", "[good]", "[NO]", "[赞]", "[来]", "[弱]", "[草泥马]", "[神马]", "[囧]", "[浮云]", "[给力]", "[围观]", "[威武]", "[奥特曼]", "[礼物]", "[钟]", "[话筒]", "[蜡烛]", "[蛋糕]"], arr = {};
    alt.forEach(function( item,index){
      arr[item] = './static/' + 'face/'+ index + '.gif';
    });
    return arr[face];
  },
  ImTxt(content){var that=this;
    content = (content||'').replace(/&(?!#?[a-zA-Z0-9]+;)/g, '&amp;')
    .replace(/</g, '&lt;').replace(/>/g, '&gt;').replace(/'/g, '&#39;').replace(/"/g, '&quot;') //XSS
    .replace(/@(\S+)(\s+?|$)/g, '@<a href="javascript:;">$1</a>$2') //转义@
    .replace(/\s{2}/g, '&nbsp') //转义空格
    .replace(/img\[http(s*)\:\/\/([^\s]+?)\.(jpg|png|gif|jpeg)\]/gi, function(img){  //转义图片
      return '<img class="layui-chuanhaiim-photos" src="' + img.replace(/(^img\[)|(\]$)/gi, '') + '">';
    })
    .replace(/face\[([^\s\[\]]+?)\]/g, function(face){  //转义表情
      var alt = face.replace(/^face/g, '');
      return '<img alt="'+ alt +'" title="'+ alt +'" src="' + that.TxtFace(alt) + '">';
    }.bind(that)).replace(/a\([\s\S]+?\)\[[\s\S]*?\]/g, function(str){ //转义链接
      var href = (str.match(/a\(([\s\S]+?)\)\[/)||[])[1];
      var text = (str.match(/\)\[([\s\S]*?)\]/)||[])[1];
      if(!href) return str;
      return '<a href="'+ href +'" target="_blank">'+ (text||href) +'</a>';
    }.bind(that)).replace(this.html(), '\<$1 $2\>').replace(this.html('/'), '\</$1\>') //转移代码
    .replace(/\n/g, '<br>') //转义换行 
    return content;
  },
  playMusic(){
    let IsPlay=true;
    let LastTime=this.getLocal('chuanhai_play_time');
    if(LastTime &&  (parseInt(Date.parse(new Date()))-parseInt(LastTime))/1000<=3  )
    {
        IsPlay=false;
    }
    if(IsPlay)
    {
        this.setLocal(Date.parse(new Date()),'chuanhai_play_time');
        var au = document.createElement("audio");
        au.preload="auto";
        au.src = "./static/sound/msg.mp3";
        au.play();
        au.remove();
    }
  },
  setLocal (res, key) {
    return window.localStorage.setItem(key, res);
  },
  getLocal (key) {
    return window.localStorage.getItem(key);
  }



}



